import IDataRequest from '../dataAccess/IDataRequest';

/**
 * Represents the base data request
 */
class BaseDataRequest implements IDataRequest {
  public operations: number;

  /**
   * Initializes a new instance of the BaseDataRequest
   * @param operations represent the operations to perform
   */
  constructor(operations: number) {
    this.operations = operations;
  }
}

export default BaseDataRequest;
